export default {
  /**
   * 
   * @param {array} objects
   */
  createAll(objects) {
    return AV.Object.saveAll(objects)
  },
  /**
   * 
   * @param {array} objects
   */
  deleteAll(objects) {
    return AV.Object.destroyAll(objects)
  },
  /**
   * 
   * @param {array} objects
   */
  updateAll(objects) {
    return AV.Object.fetchAll(objects)
  },
  /**
   * 
   * @param {array} objects
   */
  saveAll(objects) {
    try {
      return AV.Object.saveAll(objects)
    } catch(err) {
      wx.showToast({
        title: err.message,
      })
    }
  },
  /**
   * av
   * @param {string} classs 
   * @param {object} params 
   */
  create(classs, params) {
    try {
      return (new (AV.Object.extend(classs))).set(params).save()
    } catch(err) {
      wx.showToast({
        title: err.message,
      })
    }
  },
  /**
   * av
   * @param {string} classs 
   * @param {function} cbForQuery 
   */
  read(classs, cbForQuery) {
    let query = new AV.Query(classs)
    // 
    if(cbForQuery) {
      // 
      let temp_q = cbForQuery(query)
      if(temp_q) {
        query = temp_q
      }
    }
    return query.find()
  },
  /**
   * av
   * @param {string} classs 
   * @param {string} id objectId
   * @param {object} params 
   */
  update(classs, id, params) {
    let obj = AV.Object.createWithoutData(classs, id)
    // 
    for(const key in params) {
      if(params.hasOwnProperty(key)) {
        const element = params[key]
        obj.set(key, element)
      }
    }
    return obj.save()
  },
  /**
   * av
   * @param {string} classs 
   * @param {string} id objectId
   */
  delete(classs, id) {
    let obj = AV.Object.createWithoutData(classs, id)
    return obj.destroy()
  },
  /**
   * 
   * @param {string} pat 
   */
  upload(path) {
    return new AV.File(path, {
      blob: {
        uri: path,
      },
    }).save()
  },
  /**
   * av
   * @param {string} classs 
   * @param {function} cbForQuery 
   */
  count(classs, cbForQuery) {
    let query = new AV.Query(classs)
    // 
    if(cbForQuery) {
      cbForQuery(query)
    }
    return query.count()
  },
  /**
   * 
   */
  login(username, password) {
    return AV.User.logIn(username, password)
  },
  /**
   * 
   * @param {string} email 
   * @param {string} password 
   */
  loginWithEmail(email, password) {
    return AV.User.loginWithEmail(email, password)
  },
  /**
   * 
   */
  regist(username, password) {
    // 
    const user = new AV.User();

    //  user.set('username', 'Tom')
    user.setUsername(username);
    user.setPassword(password);
    return user.signUp()
  },
  /**
   * 
   */
  registWithEmail(email, password) {
    // 
    const user = new AV.User()
  
    //  user.set('username', 'Tom')
    user.setUsername(email)
    user.setPassword(password)
    
    // 
    user.setEmail(email)
    
    return user.signUp()
  },
  /**
   * 
   */
  currentUser() {
    return AV.User.current()
  },
}